.slick-arrow.slick-prev,
.slick-arrow.slick-next {
  position: absolute;
  top: 50%;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  background-image: url("../../../img/svgmin/arrow.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
}

.slick-arrow.slick-prev {
  transform: translateY(-22px) scale(-1, 1);
  left: 0;
  z-index: 2;
}

.slick-arrow.slick-next {
  right: 0;
  transform: translateY(-22px);
}

.slick-arrow.slick-prev.slick-disabled {
  transform: translateY(-22px) scale(1, -1);
  background-image: url("../../../img/svgmin/arrow-disabled.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
}

.slick-arrow.slick-next.slick-disabled {
  transform: translateY(-22px) scale(-1, 1);
  background-image: url("../../../img/svgmin/arrow-disabled.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
}

.main-header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  background-color: #44413b;
  z-index: 3;
}

.main-header__row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 6px;
  padding-bottom: 6px;
}

.main-header__burger {
  display: none;
}

.main-header::before {
  position: absolute;
  content: "";
  width: 100%;
  min-height: 79px;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: -1;
}

.navigation {
  padding-top: 12px;
  padding-bottom: 12px;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__list li {
  margin-left: 16px;
  margin-right: 16px;
}

.navigation__link {
  color: #fd9334;
  text-decoration: none;
  text-transform: uppercase;
}

.navigation__link:hover,
.navigation__link:focus {
  color: #ebc686;
  transition: all 0.3s ease 0s;
  outline: none;
}

.logo {
  width: 114px;
  height: 47px;
}

.about {
  padding-top: 100px;
  padding-bottom: 46px;
  background-color: #d9ccc1;
}

.about__row {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.about__col:first-child {
  width: 40%;
  display: flex;
  justify-content: flex-end;
}

.about__col:last-child {
  width: 60%;
  padding-left: 40px;
}

.about__img {
  width: 320px;
  height: 480px;
}

.about__title {
  margin-bottom: 12px;
  text-align: left;
}

.skills {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #ad9a8e;
}

.skills__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 70%;
  margin: 0 auto;
}

.skills__title {
  margin-bottom: 10px;
  text-align: center;
  color: #292723;
}

.skills__col {
  flex: 0 0 33.333%;
  margin-top: 22px;
}

.skills__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
}

.skills__item {
  margin-bottom: 6px;
  font-size: 20px;
  color: #292723;
  text-align: center;
}

.work-slider {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #d9ccc1;
}

.work-slider__title {
  text-align: center;
  margin-bottom: 34px;
}

.descr {
  padding-top: 26px;
  padding-bottom: 46px;
  background-color: #d9ccc1;
}

.descr__title {
  margin-bottom: 36px;
}

.descr__wrapper {
  display: flex;
  flex-direction: column;
}

.descr__project {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto;
  margin-bottom: 38px;
  padding-bottom: 28px;
  border-bottom: 1px solid #44413b;
}

.descr__project:nth-child(odd) .descr__col:first-child {
  order: 0;
}

.descr__project:nth-child(even) .descr__col:first-child {
  order: 1;
}

.descr__col:first-child {
  flex: 0 0 255px;
  display: flex;
  justify-content: flex-end;
}

.descr__col:last-child {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 0 1 70%;
  padding-left: 24px;
}

.descr__item-img {
  width: 255px;
  height: 310px;
}

.descr__info {
  margin-bottom: 8px;
  padding-left: 12px;
  font-weight: 700;
}

.descr__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 34px;
  margin-bottom: 18px;
}

.descr__item {
  margin-bottom: 2px;
}

.descr__text {
  padding-left: 12px;
}

.descr__code-link {
  text-decoration: underline;
  color: #292723;
}

.descr__code-link:hover,
.descr__code-link:focus {
  transition: all 0.2s ease 0s;
  outline: none;
  color: #ffffff;
  text-decoration: none;
}

@media (max-width: 839.98px) {
  .navigation__list li {
    margin-left: 10px;
    margin-right: 10px;
  }
}

@media (max-width: 729.98px) {
  .main-header__row {
    justify-content: center;
  }

  .main-header__burger {
    display: block;
    width: 30px;
    height: 20px;
    z-index: 4;
    background-color: transparent;
    position: absolute;
    top: 24px;
    left: 15px;
    border: none;
    padding: 0;
    cursor: pointer;
  }

  .main-header__burger:hover,
  .main-header__burger:focus {
    outline: none;
  }

  .main-header__burger span {
    background-color: #fd9334;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger::before,
  .main-header__burger::after {
    content: "";
    background-color: #fd9334;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger::before {
    top: 0;
  }

  .main-header__burger::after {
    bottom: 0;
  }

  .main-header__burger.active span {
    transform: scale(0);
  }

  .main-header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
    background-color: #ffffff;
  }

  .main-header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
    background-color: #ffffff;
  }

  .main-header.active::before {
    transition: all 0.3s ease 0s;
    background-color: #44413b;
    z-index: 4;
  }

  .navigation {
    margin-bottom: 0;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    transition: all 0.4s ease 0s;
    background-color: rgba(235, 198, 134, 0.97);
    padding: 120px 10px 20px 10px;
    z-index: 2;
  }

  .navigation.active {
    top: 0;
  }

  .navigation__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .navigation__list li {
    margin-bottom: 20px;
    margin-left: 0;
    margin-right: 0;
    position: static;
  }

  .navigation__link {
    font-size: 24px;
    font-weight: 700;
    line-height: 1.4;
    color: #44413b;
  }

  .navigation__link:hover,
  .navigation__link:focus {
    color: #ffffff;
  }

  .logo {
    position: relative;
    z-index: 4;
  }

  .descr__col:last-child {
    padding-left: 0;
  }

  .descr__item {
    font-size: 14px;
  }
}

@media (max-width: 659.98px) {
  .about {
    padding-top: 80px;
  }

  .about__row {
    flex-wrap: wrap;
  }

  .about__col:first-child {
    width: 100%;
    justify-content: center;
    margin-bottom: 36px;
  }

  .about__col:last-child {
    width: 100%;
    padding-left: 0;
  }

  .about__title {
    text-align: center;
    margin-bottom: 14px;
  }

  .about__text {
    font-size: 18px;
  }

  .skills__col {
    flex: 0 0 50%;
  }
}

@media (max-width: 569.98px) {
  .descr__project:nth-child(odd) .descr__col:first-child {
    margin-bottom: 24px;
  }

  .descr__project:nth-child(even) .descr__col:first-child {
    order: 0;
    margin-bottom: 24px;
  }

  .descr__project {
    flex-direction: column;
  }

  .descr__col:first-child {
    justify-content: center;
  }
}